import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import {
  Curb,
  LayingMain,
  SideBar,
  Slabs,
  Improvement,
  Delivery,
} from "../components/laying"
import "../components/laying/laying.css"
import { graphql } from "gatsby"

const layingBar = [
  {
    name: "Тротуарная плитка",
    title: "Монтаж тротуарной плитки",
    link: "#slabs",
  },
  {
    name: "Бордюр",
    title: "Монтаж бордюра",
    link: "#curb",
  },
  {
    name: "Благоустройство территорий",
    title: "Благоустройство территорий",
    link: "#improvement",
  },
  {
    name: "Доставка",
    title: "Доставка материалов",
    link: "#delivery",
  },
]

function LayingContent({ hash, title }) {
  const scroll = typeof window !== "undefined" ? true : false
  React.useEffect(() => {
    scroll && window.scrollTo({ top: 0 })
  }, [scroll])
  switch (hash) {
    case layingBar[0].link:
      return (
        <div className="laying__content">
          {title}
          <Slabs />
        </div>
      )
    case layingBar[1].link:
      return (
        <div className="laying__content">
          {title}
          <Curb />
        </div>
      )
    case layingBar[2].link:
      return (
        <div className="laying__content">
          {title}
          <Improvement />
        </div>
      )
    case layingBar[3].link:
      return (
        <div className="laying__content">
          {title}
          <Delivery />
        </div>
      )
    default:
      return (
        <div className="laying__content">
          {title}
          <LayingMain />
        </div>
      )
  }
}

function Laying({ location }) {
  const layingBarFiltredItem = layingBar.filter(
    item => item.link === location.hash
  )
  const layingTitle = <h1>{layingBarFiltredItem[0]?.title ?? "Монтаж"}</h1>
  return (
    <Layout>
      <Seo title="Монтаж" />
      <div className="services-image"></div>
      <section className="laying">
        <div className="container">
          <div style={{ display: "flex" }}>
            <SideBar layingBar={layingBar} hash={location.hash} />
            <LayingContent hash={location.hash} title={layingTitle} />
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default Laying

export const query = graphql`
  query LocationQuery {
    site {
      siteMetadata {
        siteUrl
      }
    }
  }
`
