import React from "react"
import { PHONE_NUMBER_MAIN } from "../consts"

function Delivery() {
  return (
    <div className="laying__content__info">
      <DistanceTable />
      <BlackSoilTable />
      <CrushedStoneTable />
      <CrumbTable />
      <SundTable />
      <style>
        {`
              th tr td, tr td td{
                  border-bottom: none;
              }
              .delivery__material__table{
                  margin-bottom: 60px;
              }
        `}
      </style>
    </div>
  )
}

export default Delivery

